<template>
    <div id="app">
        <section class="hero is-primary is-fullheight  has-bg-img ">
            <div class="hero-body" id="registerForm">
                <div class="container">
                    <div class="columns is-vcentered">
                        <div class="column">
                            <h1 class="title">
                                Menshen
                            </h1>
                            <h2 class="subtitle">
                                Register
                            </h2>
                        </div>
                        <div class="column ">

                            <form action="" @submit.prevent="register">
                                <div class="field">
                                    <label class="label">Full Name</label>

                                    <div class="control has-icons-left">
                                        <input

                                                class="input"
                                                type="input"
                                                name="inputFullName"
                                                v-model="inputFullName.data"
                                                id="inputFullName"
                                                placeholder="Full Name"
                                        />
                                        <span class="icon is-small is-left">
                      <i class="fa fa-user"></i>
                    </span>
                                    </div>
                                </div>
                                <div class="field">
                                    <label class="label">Email</label>

                                    <div class="control has-icons-left">
                                        <input
                                                @change="clearEmail"
                                                class="input"
                                                type="email"
                                                name="email"
                                                v-bind:class="{'is-danger':inputEmail.error}"
                                                v-model="inputEmail.data"
                                                id="email"
                                                placeholder="Email"
                                        />
                                        <span class="icon is-small is-left">
                      <i class="fa fa-envelope"></i>
                    </span>
                                    </div>
                                </div>
                                <p v-if="inputEmail.error" class="help is-danger">
                                    <span>{{inputEmail.errorMessage}}</span>
                                </p>
                                <div class="field">
                                    <label class="label">Password</label>

                                    <div class="control has-icons-left">
                                        <input
                                                class="input"
                                                type="password"
                                                name="password"

                                                v-model="inputPassword.data"
                                                id="password"
                                                placeholder="Password"
                                        />
                                        <span class="icon is-small is-left">
                      <i class="fa fa-lock"></i>
                    </span>
                                    </div>
                                </div>
                                <div class="field">
                                    <label class="label">Confirm your password</label>

                                    <div class="control has-icons-left">
                                        <input
                                                class="input"
                                                type="password"
                                                name="passwordConfirm"
                                                v-model="inputPasswordConfirm"
                                                id="passwordConfirm"
                                                placeholder="Confirm your password"
                                        />
                                        <span class="icon is-small is-left">
                      <i class="fa fa-lock"></i>
                    </span>
                                    </div>
                                </div>
                                <div class="is-inline">
                                    <button
                                            type="submit"
                                            class="button is-primary
                                        is-pulled-right"
                                    >
                                        Register
                                    </button>

                                    <router-link to="/login">
                                        <button
                                                class="button is-text
                                        is-pulled-right"
                                        >
                                            Already have an account?
                                        </button>
                                    </router-link>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
    export default {
        name: "registerPage",
        data() {
            return {
                inputEmail: {data: "", error: false, errorMessage: ""},
                inputPassword: {data: "", error: false, errorMessage: ""},
                inputPasswordConfirm: {data: "", error: false, errorMessage: ""},
                inputFullName: {data: "", error: false, errorMessage: ""},
                errors: [],
            };
        },
        methods: {

            register: function () {
                let body = {
                    email: this.inputEmail.data,
                    password: this.inputPassword.data,
                    name: this.inputFullName.data
                };
                let rq = {
                    method: "post",
                    url: this.appConfig.$api_url + '/api/users/register',
                    data: body
                };

                this.$http(rq).then(r => {
                    if (r.status != 201) {
                        if (r.data.message == "email already is registered.") {
                            this.inputEmail.error = true;
                            this.inputEmail.errorMessage = "This email is already registered."

                        }
                    }
                })
                    .catch(e => {
                        console.log("Error:", e)
                    })
            },
            clearEmail : function(x){
              x;
              this.inputEmail.error = false;
            }

        },

    };
</script>

<style scoped>
    .has-bg-img {
        background: linear-gradient(rgba(5, 40, 85, 0.99), rgba(5, 40, 85, 0.99)),
        url("https://media.giphy.com/media/3oKIPhesuFEOKTvx5e/giphy.gif");
    }
